import Link from 'next/link';
import Image from 'next/image';
import { Header } from '@/components/landing/header';
import { Footer } from '@/components/landing/footer';
import { Card, CardContent, CardHeader } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { format } from 'date-fns';
import { Search } from 'lucide-react';
import { Input } from '@/components/ui/input';
import { NewsletterSignup } from '@/components/blog/newsletter-signup';

// MOCK DATA - Replace with Firestore fetching
const MOCK_POSTS = [
  {
    id: '1',
    slug: 'a-weekend-in-the-mountains',
    title: 'A Weekend in the Mountains',
    excerpt: 'A refreshing experience, full of nature and tranquility. We hiked, we explored, and we connected with the great outdoors.',
    image: 'https://picsum.photos/seed/mountains/600/400',
    imageHint: 'mountains landscape',
    category: 'Weekend Getaways',
    author: 'Jane Doe',
    date: new Date().toISOString(),
  },
    {
    id: '3',
    slug: 'exploring-the-urban-jungle',
    title: 'Exploring the Urban Jungle',
    excerpt: 'City life has its own adventures. This post is about exploring the hidden gems of a bustling metropolis.',
    image: 'https://picsum.photos/seed/city/600/400',
    imageHint: 'city skyline',
    category: 'City Guides',
    author: 'Alex Ray',
    date: new Date().toISOString(),
  }
];


async function getBlogPosts(searchTerm?: string) {
    if (searchTerm) {
        return MOCK_POSTS.filter(post => 
            post.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
            post.excerpt.toLowerCase().includes(searchTerm.toLowerCase())
        );
    }
    return MOCK_POSTS;
}


export default async function BlogIndexPage({ searchParams }: { searchParams?: { search?: string } }) {
  const searchTerm = searchParams?.search || '';
  const posts = await getBlogPosts(searchTerm);

  return (
    <div className="flex flex-col min-h-dvh bg-background">
      <Header />
      <main className="flex-1">
        <section className="py-20 md:py-32 bg-secondary">
          <div className="container mx-auto text-center">
            <h1 className="text-5xl md:text-7xl font-extrabold font-headline tracking-tight text-foreground">
              Travonex Blog
            </h1>
            <p className="mt-6 max-w-2xl mx-auto text-lg md:text-xl text-foreground/80">
              Travel stories, tips, and inspiration from our community and team.
            </p>
            <div className="mt-10 max-w-lg mx-auto">
              <form>
                <div className="relative">
                  <Search className="absolute left-4 top-1/2 -translate-y-1/2 h-5 w-5 text-muted-foreground" />
                  <Input
                    type="search"
                    name="search"
                    placeholder="Search for articles..."
                    className="w-full rounded-2xl p-6 pl-12 text-lg"
                    defaultValue={searchTerm}
                  />
                </div>
              </form>
            </div>
          </div>
        </section>

        <section className="py-24 md:py-32">
          <div className="container mx-auto">
             {posts.length > 0 ? (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                {posts.map((post) => (
                    <Link href={`/blog/${post.slug}`} key={post.slug}>
                    <Card className="overflow-hidden h-full group cursor-pointer shadow-lg hover:shadow-2xl transition-shadow duration-300 rounded-2xl border-none">
                        <CardHeader className="p-0">
                         {post.image && (
                            <div className="aspect-video overflow-hidden">
                                <Image
                                src={post.image}
                                alt={post.title}
                                width={600}
                                height={400}
                                className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                                data-ai-hint={post.imageHint || 'travel landscape'}
                                />
                            </div>
                         )}
                        </CardHeader>
                        <CardContent className="p-6">
                        <div className="flex items-center gap-2 mb-2">
                            <Badge variant="secondary">{post.category}</Badge>
                        </div>
                        <h2 className="text-xl font-bold font-headline group-hover:text-primary transition-colors">{post.title}</h2>
                        <p className="mt-2 text-muted-foreground line-clamp-3">{post.excerpt}</p>
                        <div className="mt-4 flex items-center gap-4 text-sm text-muted-foreground">
                            <span>{post.author}</span>
                            <span>•</span>
                            <time dateTime={post.date}>{format(new Date(post.date), 'MMMM d, yyyy')}</time>
                        </div>
                        </CardContent>
                    </Card>
                    </Link>
                ))}
                </div>
             ) : (
                <div className="text-center">
                    <h2 className="text-2xl font-bold">No Posts Found</h2>
                    <p className="mt-4 text-muted-foreground">Try adjusting your search term or check back later!</p>
                </div>
             )}
          </div>
        </section>

        <section className="pb-24 md:pb-32">
            <div className="container mx-auto">
                <NewsletterSignup />
            </div>
        </section>
      </main>
      <Footer />
    </div>
  );
}
